import React from 'react';
class Button extends React.Component {


  call = (e) => {
    e.target.blur();
    this.props.onClick();
  }

  render(){
    return(
      <button id={this.props.id} className="btn btn-secondary" onClick={(e) => this.call(e)} >{this.props.butType}</button>
    );
  }
}

export default Button;

