import React from "react";

class TextArrea extends React.Component {
  
  render() {
    return (
      <textarea
        id="textarr"
        rows="3"
        cols="30"
        placeholder={this.props.placeholder}
        readOnly={this.props.readonly}
        value={this.props.value}
        onChange={this.props.onChange}
      />
    );
  }
}

export default TextArrea;
