import React from "react";
import Button from "./Button";
import TextArrea from "./TextArrea";



const conversionConfig = {
  decToBin : {
    placeholder : "Enter decimal number here",
    function: 'decToBin'
  },  binToDec : {
    placeholder : "Enter binary number here",
    function: 'binToDec'

  }

}
class Calculator extends React.Component {
  state = {
    value: "",
    convertedValue: "",
    conversionType: "decToBin"
  };

  decToBin = () => {
    console.log();
  };


  inputReceiver = (event) => {
   const input = (event.target.value);
    this.setState({ value:  input});

  }
  decToBin = () => {
    let val = this.state.value;
    if(val.length!==0){
    val = parseInt(val);
    let bin = '';
    let newVal = val;
    let remainder;
    
    while(newVal!==0){
        remainder = (newVal%2);
        newVal = Math.floor(newVal /= 2);
        //console.log(remainder);
        bin = remainder + bin;
    }
    
    //textArea.innerHTML = '';
    this.setState({convertedValue: bin});
    }
    this.setState({value: ''});

    console.log('CONVERTING DECIMAL TO BINARY!');
    }

    binToDec = () => {
      let textArea = document.getElementById('textarr');
      let val = this.state.value;
      val += '';
      let temp;
      let newVal = 0;
      let length = val.length;
      if(val.length!==0){
      
      for(let i=0;i<length;i++){
      temp = parseInt(val.charAt(i));
      //console.log(temp);
      newVal += temp * Math.pow(2, length-1-i);
      
      
      }
      //textArea.innerHTML = '';
      textArea.value= "";
    this.setState({convertedValue: newVal});
      //console.log(newVal);
      }

      this.setState({value: ''});

      }
    // binToDec = () => {
    //   console.log('CONVERTING BINARY TO DECIMAl!');

    // }

 

  swap = () => {
    if (this.state.conversionType === "decToBin") {
      this.setState({conversionType: 'binToDec'});
      console.log("DECIMAL TO BINARY");
    }
    else{
      this.setState({conversionType: 'decToBin'})
        console.log("BIN TO DEC");
    
    }

    this.setState({value: '', convertedValue: ''});

    //console.log(swapBtn.onclick);
  };

  
  render() {
    return (
      <div className="calc">
        <TextArrea value={this.state.value} onChange={this.inputReceiver} placeholder={conversionConfig[this.state.conversionType].placeholder} readonly={false} />
        <div className="buts">

          <Button id="sub" butType="Submit" onClick={eval('this.'+conversionConfig[this.state.conversionType].function)} />
          <Button id="swap" butType="Swap" onClick={this.swap} />
        </div>
        <TextArrea value={this.state.convertedValue} placeholder="" readonly={true} />
      </div>
    );
  }
}

export default Calculator;
